$ink: #111;
$text: #666;
$border: #eee;

.site-header {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  border-bottom: 1px solid $border;
  position: sticky;
  top: 0;
  background: #fff;
  z-index: 10;

  /* 로고 */
  > a {
    font-weight: 700;
    font-size: 1.3rem;
    color: $ink;
    text-decoration: none;
  }

  nav {
    display: flex;
    gap: 24px;

    a {
      text-decoration: none;
      font-size: 1.2rem;
      font-weight: 500;
      color: $text;
      position: relative;
      padding-bottom: 4px;
      transition: color 0.25s ease;

      &:hover {
        color: $ink;
      }

      /* underline 기본 숨김 */
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1px;
        width: 100%;
        height: 2px;
        background: $ink;
        transform: scaleX(0);
        transition: transform 0.25s ease;
        transform-origin: left;
      }

      /* 마우스 올릴 때 underline 나타남 */
      &:hover::after {
        transform: scaleX(1);
      }

      /* NavLink active */
      &.nav-active {
        color: $ink;
        font-weight: 600;

        &::after {
          transform: scaleX(1);
        }
      }
    }
  }
}
