:root {
  --paper: #F8F6F2;
  --ink: #111;
  --beige: #BFA892;
  --bronze: #C57C45;
}

.contact {
  padding: 56px 20px 96px;
  max-width: 1440px;
  margin: 0 auto;
  color: var(--ink);
}

.contact__header {
  margin-bottom: 28px;

  h1 {
    font: 700 32px/1.15 'Playfair Display', serif;
    letter-spacing: -0.01em;
    margin: 0 0 8px;
  }

  .contact__sub {
    color: #666;
    margin: 0;
    font-size: 15px;
  }
}

.contact__grid {
  display: grid;
  grid-template-columns: 1fr 1.1fr;
  gap: clamp(16px, 4vw, 28px);

  @media (max-width: 920px) {
    grid-template-columns: 1fr;
  }
}

.card {
  background: #fff;
  border: 1px solid rgba(0,0,0,.06);
  border-radius: 16px;
  padding: clamp(16px, 2.8vw, 24px);
  box-shadow: 0 8px 22px rgba(0,0,0,.06);

  h2 {
    font: 600 18px/1.3 'Pretendard', system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
    margin: 0 0 14px;
    letter-spacing: -.01em;
  }
}

.row {
  display: grid;
  grid-template-columns: 86px 1fr auto;
  align-items: center;
  gap: 10px;
  padding: 10px 0;
  border-bottom: 1px dashed rgba(0,0,0,.08);

  &:last-child { border-bottom: 0; }
}

.label {
  color: #777;
  font-size: 14px;
}

.link {
  color: var(--ink);
  text-decoration: none;
  word-break: break-all;
  position: relative;
  padding-bottom: 2px;

  &::after {
    content: '';
    position: absolute;
    left: 0; bottom: -1px; width: 100%; height: 1.5px;
    background: var(--beige);
    transform: scaleX(0);
    transform-origin: left;
    transition: transform .25s ease;
  }
  &:hover::after { transform: scaleX(1); }
}

.copy {
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid rgba(0,0,0,.08);
  background: #fff;
  font-size: 13px;
  cursor: pointer;
  transition: border-color .2s ease, transform .1s ease;

  &:hover { border-color: var(--beige); }
  &:active { transform: translateY(1px); }
}

.toast { margin-top: 10px; font-size: 13px; color: var(--bronze); }
.toast--err { color: #c65050; }

.form { display: grid; gap: 14px; }

.field {
  display: grid;
  gap: 8px;

  label { font-size: 14px; color: #444; }

  input, textarea {
    border: 1px solid rgba(0,0,0,.12);
    border-radius: 12px;
    padding: 12px 14px;
    font-size: 15px;
    background: #fff;
    outline: none;
    transition: border-color .2s ease, box-shadow .2s ease;

    &:focus {
      border-color: var(--beige);
      box-shadow: 0 0 0 3px rgba(191,168,146,.18);
    }
  }

  textarea { resize: vertical; min-height: 140px; }
}

.err { color: #c65050; font-size: 12px; }

.actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-top: 6px;
}

.btn {
  padding: 11px 16px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid transparent;
  text-decoration: none;
  display: inline-flex;
  align-items: center;

  &--primary {
    background: var(--bronze);
    color: #fff;
    border-color: var(--bronze);
  }

  &--ghost {
    background: #fff;
    color: var(--ink);
    border-color: rgba(0,0,0,.12);

    &:hover { border-color: var(--beige); }
  }
}
