:root {
    --header-h: 64px;
    --paper: #F8F6F2;
    --ink: #111;
    --beige: #BFA892;
    --bronze: #C57C45;
}

.project-page {
    width: 100%;
    min-height: 100vh;
    background: var(--paper);
    color: var(--ink);

    .inner {
        max-width: 1200px;
        margin: 0 auto;
        padding: 72px 20px 120px; /* 1.2배 */
    }
}

/* Header */
.project-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px; /* 1.2배 */

    h1 {
        font-size: 31px; /* 26px → 31px */
        font-weight: 600;
        color: var(--ink);
        letter-spacing: -0.2px;
    }

    .line {
        flex: 1;
        height: 1.5px; /* 1px → 1.5px */
        background: var(--beige);
        margin: 0 10px;
    }

    .project-tabs {
        display: flex;
        gap: 10px;

        button {
            padding: 8px 18px; /* 6x14 → 8x18 */
            border-radius: 999px;
            border: 1px solid transparent;
            background: transparent;
            color: var(--ink);
            font-size: 17px; /* 14px → 17px */
            cursor: pointer;
            transition: all 0.2s;

            &:hover { background: var(--bronze); }
            &.on { background: var(--beige); color: #fff; }
        }
    }
}

/* Section */
.project-section {
    margin-top: 72px; /* 60px → 72px */

    h2 {
        margin-bottom: 20px;
        font-size: 18px; /* 15px → 18px */
        font-weight: 500;
        color: var(--ink);
    }
}

/* Grid */
.project-grid {
    display: grid;
    gap: 30px; /* 24px → 30px */
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* 260px → 300px */
    list-style: none;
}

/* Card */
.project-card {
    a {
        display: block;
        overflow: hidden;
        border-radius: 22px; /* 18px → 22px */
        border: 1px solid var(--beige);
        background: #fff;
        transition: 0.25s;
        text-decoration: none;

        &:hover {
            transform: translateY(-4px);
            box-shadow: 0 10px 24px rgba(0, 0, 0, 0.08);
        }
    }

    .thumb {
        width: 100%;
        aspect-ratio: 16 / 9;
        background: var(--paper);
        border-bottom: 1px solid var(--beige);
        position: relative;
        overflow: hidden;

        /* ✅ 자동 스크롤 효과용으로 변경 */
        img {
            width: 100%;
            height: auto;            /* object-fit 제거 → 세로 공간 확보 */
            display: block;
            transform: translateY(0);
            transition: transform 4s ease;  /* 느리고 부드럽게 */
        }

        /* 호버 시 천천히 위로 이동 */
        /* a:hover & img 대신 카드 기준으로 안정적으로 트리거 */
        /* (부모 a에 마우스 올려도 동작) */
        /* 이동량은 -25% 정도가 자연스러움 */
    }

    /* ✅ 호버 트리거 분리: 카드에 호버하면 이미지 이동 */
    .project-card:hover .thumb img,
    &:hover .thumb img {
        transform: translateY(-25%);
    }

    .thumb {
        &.placeholder { background: var(--beige); }
    }

    .info {
        padding: 18px 20px; /* 14x16 → 18x20 */
        color: var(--ink);

        .meta {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 14px; /* 12px → 14px */

            .kind {
                padding: 3px 8px; /* 2x6 → 3x8 */
                border: 1px solid #ddd;
                border-radius: 8px; /* 6px → 8px */
                font-size: 13px; /* 11px → 13px */
            }

            .dot { color: var(--ink); }
        }

        h3 {
            font-size: 18px; /* 15px → 18px */
            font-weight: 600;
            margin: 6px 0 4px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        p {
            font-size: 16px; /* 13px → 16px */
            line-height: 1.45; /* 1.35 → 조금 더 여유 */
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
    }
}
