/* ========== Global scale (≈1.2x) ========== */
html {
  font-size: 18px;
  /* 16px → 18px */
}

:root {
  --header-h: 64px;
  --paper: #F8F6F2;
  --ink: #111;
  --beige: #BFA892;
  --bronze: #C57C45;

  --bg: var(--paper);
  --text: var(--ink);
  --muted: color-mix(in srgb, var(--ink) 60%, var(--paper) 40%);
  --accent: var(--bronze);
  --card: color-mix(in srgb, var(--paper) 90%, var(--ink) 10%);
  --shadow: 0 14px 38px rgba(0, 0, 0, 0.12);
  /* 살짝 더 깊게 */

  /* icon sizing (1.2x) */
  --icon-size: 24px;
  /* 18px → 24px */
  --icon-col: 34px;
  /* 26px → 34px */
}

@media (prefers-color-scheme: dark) {
  :root {
    --bg: #0b0b0b;
    --text: #eeeeee;
    --muted: #9ca3af;
    --card: #121212;
  }
}

/* Mixins */
@mixin card {
  background: var(--bg);
  border-radius: 18px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow);
}

@mixin tapTarget {
  border-radius: 10px;
  transition: background .2s ease, color .2s ease, transform .2s ease;

  &:active {
    transform: translateY(1px);
  }
}

/* Layout */
.wrap {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: clamp(16px, 3vw, 40px);
  width: min(1200px, 95%);
  margin: 0 auto;
  padding: 40px 0 120px;
  color: var(--text);
  position: relative;
  /* ✅ sticky 안정화 */

  @media (max-width: 900px) {
    grid-template-columns: 1fr;
  }

  .side {
    position: sticky;
    top: calc(var(--header-h) + 16px);
    /* ✅ 헤더 아래에 고정 */
    align-self: start;
    height: max-content;
    z-index: 5;

    @media (max-width: 900px) {
      position: static;
      /* 모바일에서는 자연스러운 흐름 */
    }

    .sideInner {
      @include card;
      padding: 24px;
      display: grid;
      grid-template-rows: auto 1fr;
      /* ✅ 헤더 + 스크롤 nav */
      max-height: calc(100vh - var(--header-h) - 32px);
      /* ✅ 화면 안에 맞춤 */
      overflow: hidden;

      .heading {
        font-size: 1.6rem;
        margin: 0 0 12px;
      }

      .nav {
        display: grid;
        gap: 8px;
        overflow: auto;
        /* ✅ 내부 스크롤 */
        -webkit-overflow-scrolling: touch;
        padding-right: 4px;


        .navLink {
          @include tapTarget;
          text-decoration: none;
          color: var(--muted);
          padding: 10px 12px;

          &:hover {
            background: rgba(21, 124, 62, .08);
            color: var(--accent);
          }
        }
      }
    }
  }

  .main {
    display: grid;
    gap: 32px;
  }
}

/* Sections */
.section {
  @include card;
  padding: clamp(24px, 3vw, 34px);
  padding-top: 0;

  .sectionHeader h3 {
    font-size: 1.4rem;
    margin-bottom: 10px;

    &::after {
      content: '';
      display: inline-block;
      width: 9px;
      height: 9px;
      margin-left: 9px;
      background: var(--accent);
      border-radius: 50%;
    }
  }

  .list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: grid;
    gap: 12px;

    .item {
      display: grid;
      grid-template-columns: var(--icon-col) 1fr;
      align-items: center;
      gap: 12px;
      background: var(--card);
      border-radius: 18px;
      padding: 16px 18px;
      transform: translateX(36px);
      /* 애니메이션은 item 내부로만 적용 → sticky 영향 X */
      opacity: 0;
      transition: .6s cubic-bezier(.2, .7, .2, 1);
      transition-delay: var(--d);
      cursor: pointer;

      .iconWrap {
        width: var(--icon-size);
        height: var(--icon-size);

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          display: block;
        }
      }

      .label {
        font-size: 1.06rem;
        line-height: 1.45;
      }
    }
  }

  &.in .list .item {
    transform: none;
    opacity: 1;
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .section .list .item {
    transform: none !important;
    opacity: 1 !important;
    transition: none !important;
  }
}

/* ---------- Post-it Modal (Anti-Blur + 1.2x) ---------- */
.postit-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(133, 132, 132, 0.2);
  backdrop-filter: blur(1.3px);
  z-index: 10000;
}

/* 내용은 0°, 종이만 기울이기 (선명도) */
.postit {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: min(560px, 92vw);

  padding: 40px 32px 30px;
  /* 확대 반영 */
  color: var(--ink);
  z-index: 10020;
  isolation: isolate;
  border-radius: 12px;
  margin: 0 auto;
}

/* 배경 종이만 살짝 기울임 */
.postit::after {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--paper);
  border-radius: 12px;
  border: 1px solid rgba(0, 0, 0, .08);
  box-shadow: 0 22px 68px rgba(0, 0, 0, .18), 0 2px 0 rgba(0, 0, 0, .05);
  transform: rotate(-1.1deg);
  z-index: -1;
}

/* 테이프 */
.postit:before {
  content: "";
  position: absolute;
  left: 40%;
  top: -12px;
  width: 136px;
  height: 30px;
  background: rgba(255, 229, 170, .92);
  border: 1px dashed rgba(0, 0, 0, .08);
  transform: rotate(2deg);
  border-radius: 8px;
}

/* Close */
.postit-close {
  position: absolute;
  right: 12px;
  top: 10px;
  background: none;
  border: 0;
  cursor: pointer;
  font-size: 24px;
  color: var(--ink);
}

/* Pin */
.postit-pin {
  position: absolute;
  left: 20px;
  top: 16px;
  width: 12px;
  height: 12px;
  background: var(--bronze);
  border-radius: 50%;
  box-shadow: 0 2px 0 rgba(0, 0, 0, .15);
}

/* Header */
.postit-header {
  display: flex;
  align-items: center;
  gap: 14px;
  padding-left: 18px;

  img {
    width: 34px;
    height: 34px;
    object-fit: contain;
    image-rendering: -webkit-optimize-contrast;
  }

  h4 {
    margin: 0;
    font-size: 1.3rem;
    letter-spacing: .2px;
  }
}

/* Skill Level Bar */
.postit-meter {
  position: relative;
  height: 12px;
  background: rgba(0, 0, 0, .06);
  border-radius: 999px;
  margin: 14px 18px 10px;

  &__bar {
    height: 100%;
    background: linear-gradient(90deg, var(--bronze), color-mix(in srgb, var(--bronze) 70%, #fff 30%));
    border-radius: 999px;
  }

  &__text {
    position: absolute;
    right: 10px;
    top: -22px;
    font-size: .95rem;
    color: var(--ink);
  }
}

.postit-desc {
  margin: 12px 18px 6px;
  line-height: 1.75;
  font-size: 1.06rem;
}

/* (보호) 모션 최소화 환경 */
@media (prefers-reduced-motion: reduce) {
  .postit {
    /* animation 없음 */
  }
}