@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap');

:root {
  --paper: #f8f6f2;
  --ink: #111;
  --beige: #bfa892;
  --bronze: #c57c45;
}

/* 화면 중앙 정렬 */
.profileWrap {
  height: calc(98.4vh - 64px);
  display: grid;
  place-items: center;
  margin: 0 auto;
  box-sizing: border-box;
}

/* 메인 카드 */
.profileCard {
  display: grid;
  grid-template-columns: clamp(340px, 32vw, 420px) 1px minmax(0, 1fr);
  width: min(1180px, 100%);
  background: var(--paper);
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 10px 26px rgba(0, 0, 0, .1);
  position: relative;
  overflow: visible;
  box-sizing: border-box;

  /* 포스트잇 */
  .postIt {
    position: absolute;
    top: -26px;
    right: -50px;
    width: 250px;
    height: 250px;
    padding: 10px;
    background: var(--bronze);
    color: var(--ink);
    border-radius: 2px;
    border: 1px solid rgba(0, 0, 0, .08);
    box-shadow: 0 10px 18px rgba(0, 0, 0, .12);
    transform: rotate(-8deg);
    white-space: pre-line;
    z-index: 5;

    p {
      transform: rotate(8deg);
      padding: 0 10px;
      font-size: 1.4rem;
      line-height: 1.1;
      text-wrap: nowrap;
      font-weight: 450;
      font-family: "Nanum Pen Script", cursive;
    }

    &::before {
      content: "";
      position: absolute;
      top: -17px;
      left: 85px;
      width: 100px;
      height: 30px;
      background: rgba(255, 229, 170, 1);
      border: 1px dashed rgba(0, 0, 0, .08);
      border-radius: 3px;
      transform: rotate(10deg);
      box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 5px;
      right: 7px;
      width: 250px;
      height: 250px;
      padding: 4px;
      background: var(--paper);
      border: 1px solid rgba(0, 0, 0, .08);
      border-radius: 5px;
      transform: rotate(4deg);
      filter: blur(.2px);
      z-index: -4;
    }
  }

  /* 왼쪽 사진 */
  .leftPage {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 0;

    .profileImage {
      width: 100%;
      height: auto;
      object-fit: cover;
      border-radius: 12px;
      box-shadow: 0 4px 14px rgba(0, 0, 0, .12);
    }
  }

  /* 구분선 */
  .divider {
    width: 1px;
    background: rgba(0, 0, 0, .2);
    margin: 0 20px;
  }

  /* 오른쪽 페이지 */
  .rightPage {
    position: relative;
    padding: 8px 12px 8px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    min-width: 0;
    overflow: hidden;

    .profileTitle {
      text-align: center;
      font-size: 2rem;
      font-weight: 700;
      letter-spacing: -1px;
      color: var(--ink);
      margin-bottom: auto;
      font-family: "Salsa", cursive;
    }

    /* ===== 기본(ABOUT) : 2열 그리드 ===== */
    .infoList {
      display: flex;
      flex-direction: column;
      gap: 14px;

      .infoRow {
        display: grid;
        grid-template-columns: 140px minmax(0, 1fr);
        align-items: center;
        column-gap: 14px;
        font-size: 1.3rem;
        border-bottom: 1px solid rgba(0, 0, 0, .14);
        padding: 10px 0;

        .label {
          font-weight: 600;
          letter-spacing: 1.6px;
          color: var(--beige);
          font-size: 1.3rem;
          white-space: nowrap;
        }

        .value {
          font-weight: 500;
          color: var(--ink);
          font-size: 1.5rem;
          min-width: 0;
          overflow-wrap: anywhere;
          text-overflow: ellipsis;
          font-family: "Gowun Dodum", sans-serif;
        }
      }
    }

    /* ===== EDUCATION/TRAINING 전용 : 세로형 + 밑줄이 칸 전체로 쭉 ===== */
    .infoList.verticalMode {
      .infoRow {
        display: flex;
        flex-direction: column;
        /* 라벨 위, 값 아래 */
        align-items: stretch;
        /* 전체 폭 채우게 */
        gap: 8px;
        padding: 14px 0;
        border: 0;
        /* 행 기본 밑줄 제거 */
      }

      .label {
        display: block;
        width: 100%;
        font-weight: 600;
        letter-spacing: 1.6px;
        color: var(--beige);
        font-size: 1.4rem;
        padding: 6px 0;
        border-bottom: 1px solid rgba(0, 0, 0, .14);
      }

      .value {
        display: block;
        width: 100%;
        font-weight: 500;
        color: var(--ink);
        font-size: 1.35rem;
        line-height: 1.5;
        white-space: normal;
        word-break: keep-all;
        overflow-wrap: break-word;
        padding: 6px 0;
        border-bottom: 1px solid rgba(0, 0, 0, .14);
        /* ✅ 값 밑줄(전체폭) */
      }

      /* 혹시 이전 after 라인들이 남아있으면 비활성화 */
      .label::after,
      .value::after,
      .infoRow::after {
        content: none !important;
      }
    }

    /* 다음 버튼 */
    .nextBtn {
      position: absolute;
      right: 5px;
      top: 50%;
      transform: translateY(-50%);
      width: 46px;
      height: 46px;
      border: none;
      border-radius: 50%;
      background: var(--bronze);
      display: grid;
      place-items: center;
      color: #fff;
      font-size: 1.6rem;
      cursor: pointer;
      transition: transform .18s ease;
      z-index: 2;

      &:hover {
        transform: translateY(-50%) scale(1.12);
      }
    }
  }
}

/* 반응형 */
@media (max-width: 900px) {
  .profileCard .postIt {
    right: 6px;
    /* 화면 작아지면 안쪽으로 */
    top: -18px;
    transform: rotate(3deg) scale(.92);
  }
}

@media (max-width: 768px) {
  .profileCard {
    grid-template-columns: 1fr;
    gap: 20px;
    width: min(600px, 100%);
    padding: 22px;

    .postIt {
      position: static;
      /* 모바일에선 카드 위로 */
      align-self: end;
      margin: -6px 0 8px auto;
      transform: rotate(2deg) scale(.9);
    }
  }

  .profileCard .divider {
    display: none;
  }

  .profileCard .rightPage {
    .nextBtn {
      right: 10px;
      top: 10px;
      transform: none;
    }

    .infoList .infoRow {
      grid-template-columns: 110px minmax(0, 1fr);
    }
  }
}