/* ================= Project01 (Web) – SCSS ================= */
:root {
  --paper: #f8f6f2;
  --ink: #111;
  --beige: #bfa892;
  --bronze: #c57c45;
  --line: #e8e2dc;

  /* 컨테이너/패딩 통일 */
  --maxw: 1440px;
  --padX: 60px;
}

.projectwrap {
  background: var(--paper);
  color: var(--ink);
  padding-bottom: var(--padX);

  /* ---------- 상단 헤더(좌 타이틀 / 우 breadcrumb) ---------- */
  .pda-top {
    width: 100%;
    max-width: var(--maxw);
    margin: 0 auto;
    padding: var(--padX);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 16px;

    #pda-title {
      margin: 0;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.3px;
      color: var(--ink);
    }

    /* breadcrumb (오른쪽) */
    .pda-crumb {
      display: flex;
      gap: 8px;
      align-items: center;
      font-size: 17px;
      color: #777;

      .crumb {
        color: #777;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }

      .is-current {
        color: #aaa;
        pointer-events: none;
      }
    }
  }

  /* ---------- 본문 ---------- */
  .pda {
    &.wrap {
      /* 필요 시 본문 패딩을 따로 두고 싶을 때 사용 */
      /* max-width / 양옆 패딩을 여기서도 맞추고 싶다면 주석 해제
      width: 100%;
      max-width: var(--maxw);
      margin: 0 auto;
      padding: 28px var(--padX) 80px;
      */
    }
  }

  /* 2 columns: 1.3fr / 1fr (요청사항 반영) */
  .pda-grid {
    width: 100%;
    max-width: var(--maxw);
    margin: 0 auto;
    color: var(--ink);
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 50px;
  }

  /* LEFT */
  .pda-main {
    min-width: 0;
    display: flex;
    flex-direction: column;
    gap: 30px;

    /* ---------- Hero: device frame ---------- */
    .hero {
      margin-bottom: 16px;

      .device {
        border: 1px solid var(--line);
        border-radius: 18px;
        background: #fff;
        overflow: hidden;
        box-shadow: 0 10px 26px rgba(0, 0, 0, .06);

        .device-topbar {
          height: 18px;
          display: flex;
          align-items: center;
          justify-content: center;

          .cam {
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: #222;
            opacity: .15;
          }
        }

        .screen {
          position: relative;
          width: 100%;
          aspect-ratio: 16 / 10;
          overflow: hidden;

          /* ✅ 자연스러운 세로 패닝: object-position 사용 */
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            /* 기본은 상단에서 시작 */
            object-position: 50% 0%;
            transition: object-position 5s ease;
            /* hover 시 아래로 '흐름' */
            will-change: object-position;

            &.panY {
              /* panY 클래스가 부드럽게 움직이게 */
              /* (필요 시 별도 설정 가능) */
            }
          }

          &:hover img.panY {
            object-position: 50% 100%;
          }
        }

        .device-foot {
          height: 12px;
          background: linear-gradient(180deg, #fff, #fbfaf8);
        }
      }

      /* hero chips */
      .hero-badges {
        display: flex;
        gap: 10px;
        margin-top: 10px;

        .chip {
          font-size: 16px;
          padding: 8px 15px;
          border-radius: 999px;
          border: 1px solid var(--line);
          background: #fff;
          color: #333;

          &.solid {
            background: var(--bronze);
            border-color: var(--bronze);
            color: #fff;
          }
        }
      }
    }

    /* 본문 텍스트/블록 */
    .desc {
      line-height: 1.8;
      color: #272727;
      font-size: 1.2rem;
    }

    .block {
      margin-top: 20px;

      h2 {
        font-size: 15px;
        letter-spacing: -.1px;
        color: #555;
        margin: 0 0 10px;
      }
    }

    /* 주요기능 리스트 */
    .featureList {
      display: grid;
      gap: 8px;
      padding: 0;
      margin: 0;

      .feature {
        list-style: none;
        display: flex;
        gap: 10px;
        align-items: flex-start;

        .dot {
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: var(--beige);
          margin-top: 7px;
        }
      }
    }

    /* Process */
    .timeline {
      ol {
        margin: 0;
        padding-left: 18px;
      }

      li {
        margin: 6px 0;
      }
    }

    /* Gallery */
    .gallery {
      .grid {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 10px;
      }

      .card {
        border: 1px solid var(--line);
        border-radius: 16px;
        background: #fff;
        box-shadow: 0 6px 18px rgba(0, 0, 0, .05);
        font-size: 16px;
        text-wrap: nowrap;
        text-decoration: none;
        color: #222;
      }

      .card:hover {
        color: var(--bronze);
      }
    }
  }

  /* RIGHT: sticky info card */
  .pda-aside {
    position: relative;

    .infoCard {
      position: sticky;
      /* grid 컬럼 폭에 맞춰 자연스럽게 */
      top: 120px;
      /* 헤더/여백 피해서 적절한 값 */
      border: 1px solid var(--line);
      border-radius: 16px;
      background: #fff;
      box-shadow: 0 8px 22px rgba(0, 0, 0, .06);
      padding: 16px 16px 18px;
      max-width: 560px;
      /* 너무 넓어지는 것 방지 */

      h3 {
        font-size: 14px;
        margin: 2px 2px 8px;
        color: #444;
      }

      hr {
        border: none;
        border-top: 1px solid #f0ede9;
        margin: 14px 0;
      }

      .links {
        display: flex;
        gap: 8px;
        flex-wrap: wrap;
      }

      .btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        padding: 10px 14px;
        border-radius: 10px;
        border: 1px solid #e6e2dc;
        background: #fff;
        color: var(--ink);
        font-weight: 600;
        font-size: 14px;
        text-decoration: none;
        transition: .18s;

        &:hover {
          transform: translateY(-1px);
          box-shadow: 0 6px 14px rgba(0, 0, 0, .06);
        }

        &.primary {
          background: var(--bronze);
          border-color: var(--bronze);
          color: #fff;
        }
      }

      .meta {
        list-style: none;
        padding: 0;
        margin: 0;
        display: grid;
        gap: 8px;

        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 12px;

          span {
            color: #666;
            font-size: 13px;
          }

          b {
            font-size: 13px;
          }
        }
      }
    }
  }

  /* ---------- Responsive ---------- */
  @media (max-width: 1280px) {
    :root {
      --padX: 48px;
    }

    .pda-top {
      padding: 48px var(--padX) 0;
    }

    .pda.wrap {
      padding: 28px var(--padX) 80px;
    }
  }

  @media (max-width: 1024px) {
    .pda-top {
      flex-direction: column;
      align-items: flex-start;
      gap: 6px;
      margin-bottom: 8px;
    }

    .pda-grid {
      grid-template-columns: 1fr;
      gap: 20px;
    }

    .pda-aside .infoCard {
      position: static;
      max-width: none;
    }
  }

  @media (max-width: 640px) {
    :root {
      --padX: 20px;
    }

    .pda-top #pda-title {
      font-size: 26px;
    }

    .pda-top .pda-crumb {
      font-size: 13px;
    }

    .pda-main .desc {
      font-size: 14px;
    }
  }
}

.stack {
  display: flex;
  gap: 15px;
}

.dual-phones {
  display: flex;
  flex-direction: row;
  gap: 30px;

  .phone {
    width: 393px;
    height: 852px;
    overflow: hidden;
    position: relative;
    border-radius: 5px;

    .phone-screen {
      width: 100%;
      height: 100%;
      overflow: hidden; // ✅ 내부 안전

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 0%;
        transition: object-position 5s ease;
        display: block;
      }
    }
  }

  .phone:hover .phone-screen img {
    object-position: 50% 100%;
  }
}