@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&family=Pretendard:wght@400;600;700&display=swap');

:root {
  --header-h: 64px;
  --paper: #F8F6F2;
  --ink: #111;
  --beige: #BFA892;
  --bronze: #C57C45;
}

.cover {
  position: relative;
  
  height: calc(98.4vh - var(--header-h));
  margin: 0 auto;

  padding: 0 38px 0 38px;
  overflow: hidden;
  box-sizing: border-box;


  /* .topbar {
    display: flex;
    justify-content: space-between;
    color: #6a6a6a;
    border-bottom: 1px solid #d9d2c8;
    padding-bottom: 5px;
    letter-spacing: .06em;
  }
 */
  .title {
    margin-top: 56px;
    max-width: 660px;
    line-height: 1.2;

    .script {
      display: block;
      font-family: 'Playfair Display', serif;
      font-style: italic;
      font-size: 48px;
      color: var(--bronze);
      margin-bottom: 6px;
    }

    .main {
      font-family: 'Playfair Display', serif;
      font-size: 36px;
      font-weight: 700;
      margin: 0;
      letter-spacing: -0.01em;
      line-height: 1.08;
    }

    .sub {
      margin-top: 16px;
      /* 10px → 16px */
      font-size: 56px;
      color: var(--beige);
      font-family: 'Playfair Display', serif;
      letter-spacing: .02em;
      /* .04em → .02em */
      white-space: nowrap;
      /* text-wrap 대신 */
      line-height: 1;
    }
  }

  /* ===== 오른쪽 배경 이미지 ===== */
  .heroImage {
    position: absolute;
    inset: 0 0 0 48%;
    z-index: -1;

    background-size: cover;
    background-position: center 5%;
    background-repeat: no-repeat;

    /* 그라데이션 오버레이(이미지와 분리) */
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      background: linear-gradient(90deg,
          rgba(248, 246, 242, .88) 0%,
          rgba(248, 246, 242, .10) 35%,
          rgba(0, 0, 0, .20) 100%);
      pointer-events: none;
    }
  }

  /* ===== 폴라로이드 카드 =====
     고정 px 대신 clamp로 반응형 위치 보정 */
  .polaroid {
    position: absolute;
    left: clamp(640px, 56vw, 860px);
    top: clamp(220px, 28vh, 340px);
    width: clamp(240px, 28vw, 320px);
    aspect-ratio: 3 / 4;
    transform: rotate(-4deg);
    filter: drop-shadow(0 18px 28px rgba(0, 0, 0, .18));

    &::before {
      content: "";
      position: absolute;
      inset: -14px;
      background: #fff;
      border-radius: 3px;
      box-shadow: inset 0 1px 0 rgba(0, 0, 0, .06);
    }

    .polaroid__img {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 2px;
    }
  }

  .clip {
    position: absolute;
    width: 44px;
    height: 44px;
    right: -10px;
    top: -28px;
    z-index: 2;

    &::before,
    &::after {
      content: "";
      position: absolute;
      inset: 0;
      border: 2px solid #b3b3b3;
      border-radius: 24px;
      transform: rotate(20deg);
    }

    &::after {
      inset: 7px 5px 5px 7px;
    }
  }

  .stamp {
    position: absolute;
    right: 50px;
    bottom: 50px;
    width: 140px;
    height: 140px;
    border: 2px solid var(--beige);
    border-radius: 50%;
    display: grid;
    place-items: center;
    text-align: center;
    font: 600 14px/1.1 'Playfair Display', serif;
    color: var(--beige);
    opacity: .7;
    letter-spacing: .08em;
    transform: rotate(-8deg);
  }

  .credit {
    position: absolute;
    left: 64px;
    bottom: 64px;
    font-size: 12px;
    color: #777;
  }
}

/* ===== 반응형 튜닝 ===== */
@media (max-width: 1280px) {
  .cover {
    .heroImage {
      inset: 0 0 0 50%;
      background-position: center 60%;
    }

    .polaroid {
      left: clamp(520px, 56vw, 740px);
    }
  }
}

@media (max-width: 1024px) {
  .cover {
    .title .main {
      font-size: 36px;
    }

    .title .sub {
      font-size: 40px;
    }

    .heroImage {
      inset: 35% 0 0 0;
      background-position: center 55%;
    }

    .polaroid {
      left: auto;
      right: 24px;
      top: clamp(120px, 18vh, 180px);
      width: 240px;
    }
  }
}

@media (max-width: 520px) {
  .cover {
    padding: 36px 24px 28px;

    .title {
      .script {
        font-size: 32px;
      }

      .main {
        font-size: 28px;
      }

      .sub {
        font-size: 28px;
      }
    }

    .polaroid {
      right: 16px;
      top: clamp(110px, 22vh, 160px);
      width: 180px;
    }

    .stamp {
      width: 110px;
      height: 110px;
      right: 16px;
      bottom: 24px;
    }
  }
}

/* 큰 화면에서 비율 유지 */
@media (min-width: 1440px) {
  .cover {
    .heroImage {
      inset: 0 0 0 50%;
    }

    .polaroid {
      left: clamp(720px, 52vw, 900px);
    }
  }
}

/* (선택) 폴라로이드 호버 미세 모션 */
@media (hover:hover) {
  .polaroid {
    transition: transform .35s ease, filter .35s ease;
  }

  .polaroid:hover {
    transform: rotate(-3deg) translateY(-4px);
    filter: drop-shadow(0 22px 34px rgba(0, 0, 0, .20));
  }
}

