.tintro {
  position: fixed;
  inset: 0;
  background: #f8f6f2;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 9999;
  transition: opacity .6s ease;
}

.tintro.tintro--out {
  opacity: 0;
  pointer-events: none;
}

/* 중앙 텍스트 */
.tintro__inner {
  position: relative;
  z-index: 5;
  text-align: left;
  max-width: 540px;
  margin-left: -8vw;
  font-family: 'Playfair Display', serif;
}

.tintro__inner .tintro__line {
  font-size: 38px;
  color: #333;
  line-height: 1.45;
  white-space: pre-wrap;
}

.tintro__inner .tintro__line:nth-child(1),
.tintro__inner .tintro__line:nth-child(2) {
  font-family: 'Playfair Display', serif;
  color: #c57c45;
  /* 부드러운 브론즈 */
  font-style: italic;
  font-weight: 600;
}

.tintro__inner .cursor {
  display: inline-block;
  animation: blink 0.9s step-end infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* ✅ 드로잉 위치 및 크기 */
.dogsketch {
  width: 50%;
  height: auto;
}

/* 기본은 완료 상태(정지). on이 붙을 때만 애니메이션 재생 */
.dogsketch .draw {
  stroke: #222;
  stroke-width: 2px;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 1;
  /* pathLength=1 기준 */
  stroke-dashoffset: 0;
  /* 기본 완료 */
  animation: none;
}

.dogsketch.on .draw {
  stroke-dashoffset: 1;
  /* 시작: 전부 감춤 */
  animation: dog-draw 2.8s ease forwards;
}

/* 순차 지연 */
.dogsketch.on .draw:nth-child(1) {
  animation-delay: 0s;
}

.dogsketch.on .draw:nth-child(2) {
  animation-delay: .12s;
}

.dogsketch.on .draw:nth-child(3) {
  animation-delay: .24s;
}

.dogsketch.on .draw:nth-child(4) {
  animation-delay: .36s;
}

.dogsketch.on .draw:nth-child(5) {
  animation-delay: .48s;
}

@keyframes dog-draw {
  to {
    stroke-dashoffset: 0;
  }
}

/* 건너뛰기 버튼 */
.tintro__skip {
  position: absolute;
  right: 20px;
  bottom: 20px;
  padding: 6px 14px;
  font-size: 13px;
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid #ddd;
  cursor: pointer;
}

/* ─────────────────────────────────────────── */
/* ✨ sparkle styles (왼쪽 눈 하이라이트)      */
/* ─────────────────────────────────────────── */

.dogsketch .eye-sparkle {
  pointer-events: none;
  /* 클릭 방지 */
  transform-origin: center;
}

.dogsketch .eye-sparkle .sparkle-ring {
  fill: none;
  stroke: #111;
  /* 링 색 */
  stroke-width: 1.6px;
  opacity: 0;
}

.dogsketch .eye-sparkle .sparkle-star line {
  stroke: #111;
  /* 별 선 색 */
  stroke-width: 1.6px;
  stroke-linecap: round;
  opacity: 0;
}

/* on 상태에서만 반짝 등장/펄스 */
.dogsketch .eye-sparkle .sparkle-ring {
  fill: none;
  stroke: #222;
  stroke-width: 2px;
}

.dogsketch .eye-sparkle .sparkle-star line {
  stroke: #222;
  stroke-width: 2px;
  stroke-linecap: round;
}

/* 등장 */
@keyframes ring-pop {
  from {
    transform: scale(.4);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: .9;
  }
}

@keyframes star-pop {
  from {
    transform: scale(.2) rotate(0deg);
    opacity: 0;
  }

  to {
    transform: scale(1) rotate(12deg);
    opacity: 1;
  }
}

/* 반복 펄스 & 반짝 */
@keyframes ring-pulse {

  0%,
  100% {
    transform: scale(1);
    opacity: .9;
  }

  50% {
    transform: scale(1.08);
    opacity: 1;
  }
}

@keyframes star-twinkle {

  0%,
  100% {
    transform: rotate(12deg) scale(1);
    opacity: 1;
  }

  50% {
    transform: rotate(12deg) scale(1.06);
    opacity: .85;
  }
}